.choices.select-choices {
    color: #1d1d1d;
    max-width: 358px;
    width: 100%;
}
.choices.select-choices::after {
    border-color: $primary-color transparent transparent transparent;
    border-width: 10px 7px;
    margin-top: 0;
    transform: translateY(-5px);
    z-index: 1;
}
.choices.select-choices .choices__list--dropdown {
    border: 1px solid $primary-color;
    box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.162096);
    border-radius: 8px;
    padding-bottom: 10px;
    top: 1px;
}
.choices.select-choices .choices__heading {
    color: $primary-color;
    border: 0 none;
    padding: 10px 18px 18px;
}
.choices.select-choices .choices__heading::after {
    content: ":";
}
.choices.select-choices .choices__item.is-highlighted {
    background: #f2f2f2;
}
.choices.select-choices .choices__item,
.choices.select-choices .choices__list--dropdown .choices__item {
    padding: 2px 18px;
    min-height: 40px;
}
.choices.select-choices .choices__inner {
    border: 1px solid $primary-color;
    border-radius: 8px;
    min-height: 40px;
    padding-left: 0;
    padding-right: 0;
    background-color: #fff;
}
.choices.select-choices .choices__inner .choices__list--single {
    padding-left: 0;
    padding-right: 0;
}
.choices.select-choices .choices__inner .choices__item {
    min-height: 0;
}
.choices.select-choices.is-open .choices__inner {
    border-color: $primary-color;
    border-radius: 8px 8px 0 0;
}
.choices.select-choices.is-open .choices__list--dropdown {
    border-color: $primary-color;
}
.choices.select-choices.is-open[data-type*="select-one"]::after {
    border-color: transparent transparent $primary-color transparent;
    transform: translateY(-10px);
}
.choices.select-choices.is-focused .choices__inner {
    border-color: $primary-color;
}
